.box
  @apply bg-white
  &--sm
    @apply py-5 px-7 rounded-[1.25rem]
    box-shadow: 0px 6px 30px 0px rgba(0, 0, 0, 0.05)
  &--xl
    @apply p-10 xl:py-14 xl:px-14 rounded-[1.875rem]
    box-shadow: -6px 6px 50px 0px rgba(0, 0, 0, 0.06)
  &--info
    @apply p-10 xl:py-7 xl:pl-[80px] xl:pr-14 rounded-[1.25rem] 
    box-shadow: -6px 6px 50px 0px rgba(0, 0, 0, 0.06)

.social-links
  @apply flex gap-4